<template>
  <div class="sdk-track-demo" :class="{ 'is-miniprogram': isMiniProgram || isSmallScreen }">
    <div class="block">
      <h2>
        初始化SDK:
      </h2>
      <div>
        <div v-if="false">
          <strong>tid(主号)</strong>
          <el-input v-model="tid" placeholder="tid(主号)" />
        </div>

        <div>
          <strong>是否内嵌到微信小程序中: {{ isMiniProgram }}</strong>
        </div>
        <div>
          <strong>内嵌到微信小程序时参数的位置</strong>
          <el-checkbox name="afterHash" v-model="defaultOptions.afterHash">是否将参数放到URL的hash指之后</el-checkbox>
        </div>

        <div>
          <strong>appkey</strong>
          <el-input v-model="appkey" placeholder="appkey" />
        </div>
        <div>
          <strong>默认页面事件开关(pageview | pageclose)</strong>
          <el-checkbox v-model="defaultOptions.preventAutoTrack">是否停止上报默认页面事件</el-checkbox>
        </div>
        <div>
          <strong>页面停留开关</strong>
          <el-checkbox v-model="defaultOptions.pagestay">页面停留开关</el-checkbox>
        </div>
        <div>
          <strong>appid</strong>
          <el-input v-model="defaultOptions.appid" placeholder="appid" />
        </div>
        <div>
          <strong>openid</strong>
          <el-input v-model="defaultOptions.openid" placeholder="openid" />
        </div>
        <div>
          <strong>useOpenId</strong>
          <el-checkbox v-model="defaultOptions.useOpenId">没有openid就不上报</el-checkbox>
        </div>
        <div>
          <strong>加密类型</strong>
          <el-radio-group v-model="defaultOptions.encrypt_mode">
            <el-radio label="close">不加密</el-radio>
            <el-radio label="default">base64加密</el-radio>
            <el-radio label="aes">aes加密</el-radio>
          </el-radio-group>
<!--          <el-radio-group v-model="defaultOptions.encrypt_mode"-->
<!--                         :options="[{-->
<!--            value: 'close',-->
<!--            label: '不加密',-->
<!--          },-->
<!--          {-->
<!--            value: 'default',-->
<!--            label: 'base64加密',-->
<!--          },-->
<!--          {-->
<!--            value: 'aes',-->
<!--            label: 'aes加密',-->
<!--          }]"></el-radio-group>-->
        </div>
        <div>
          <strong>是否前端压缩</strong>
          <el-checkbox v-model="defaultOptions.enable_compression">开启发送前压缩</el-checkbox>
        </div>
        <div>
          <strong>发送最小间隔(ms)</strong>
          <el-input v-model="defaultOptions.track_interval" placeholder="track_interval" />
        </div>
        <div>
          <strong>批量发送数量</strong>
          <el-input v-model="defaultOptions.batch_max_time" placeholder="batch_max_time" />
        </div>
        <div>
          <p>自定义域名地址</p>
          <el-input v-model="defaultOptions.url" placeholder="域名" />
        </div>
        <div>
          <p>session间隔时长（ms, 默认30mins）</p>
          <el-input v-model="defaultOptions.session_interval" placeholder="session间隔时长" />
        </div>
      </div>
      <strong>
        <el-button @click="initTracker">初始化sdk</el-button>
        <el-button @click="reload">重置sdk</el-button>
      </strong>
      <div>
        <div>
          +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++
        </div>
        生成配置项：
        <div>
          {{ defaultOptions }}
        </div>
        <div>
          +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++
        </div>
      </div>
    </div>
    <div class="block">
      <h2>
        用户信息 & 本地信息区域:
      </h2>
      <div>
        当前的anonymous_id：
        <p class="qid-value">{{ anonymous_id || '-' }}</p>
      </div>
      <el-button @click="updateInfo">更新当前anonymous_id</el-button>
      <el-button @click="clearLocalInfo">清空当前本地存储信息</el-button>
    </div>
    <div class="block">
      <h2>
        手动上报区域：
      </h2>
      <p><a href="./test1.html?_type=wpa" target="_blank">新页面打开 Test1</a></p>
      <p><a href="./test2.html">本页面打开 Test2</a></p>
      <el-button @click="jumpSPA">单页跳转</el-button>
      <h4>自定义参数</h4>
      <div
        v-for="(item, index) in customData" :key="index + ':customData'"
      >
        <!-- <el-input-group name="customData" separate> -->
          <el-input :name="index + '-customData-key'" v-model="item.key" placeholder="key" />
          <el-input :name="index + '-customData-value'" v-model="item.value" placeholder="value" />
        <!-- </el-input-group> -->
      </div>
      <el-button @click="customData.push({key: '', value: ''})">添加一个自定义参数</el-button>
      <el-button @click="setCommonData">set更新自定义参数</el-button>
      <h4>自定义用户ID</h4>
      <div
          v-for="(item, index2) in accountData" :key="index2 + ':accountData'"
      >
        <!-- <el-input-group name="accountData" separate> -->
          <el-input :name="index2 + '-accountData-key'" v-model="item.key" placeholder="key" />
          <el-input :name="index2 + '-accountData-value'" v-model="item.value" placeholder="value" />
        <!-- </el-input-group> -->
      </div>
      <el-button @click="accountData.push({key: '', value: ''})">添加一个自定义参数</el-button>
      <el-button @click="setAccountData">set更新自定义参数</el-button>
      <el-button @click="sendEvent1">按钮1自定义事件</el-button>
      <el-button @click="sendEvent2">按钮2 10条批量上报 & 上报间隔测试</el-button>
      <el-input v-model="trackNumber"></el-input>
      <el-button @click="sendEvent3(trackNumber)">按钮3 n条批量上报</el-button>
      <p>
        <a href="http://q.url.cn/s/WLd3nCm?_type=wpa" target="_blank">打开一个链接型接待组件</a>
        <a href="http://q.url.cn/s/WLd3nCm?_type=wpa" target="_blank"><el-button>打开一个链接型接待组件 2</el-button></a>
      </p>

      <div class="block">
        <div>
          <strong>调试webworker定时器</strong>
          <el-input v-model="timeout" />
          <el-button @click="testWebWorker">测试定时器</el-button>
          <el-input v-model="timerId" />
          <el-button @click="clearTimer">取消定时器</el-button>
        </div>
      </div>
    </div>
    <div>
    </div>
  </div>
</template>
<script>
import QDTracker from '../../../web/QDTracker-dev'
import './aes.js'

export default {
  name: 'demo',
  data () {
    return {
      timeout: 5000,
      timerId: 0,
      count: 0,
      isMiniProgram: false,
      isSmallScreen: false,
      // 初始化参数
      tid: '',
      appkey: '0WEB05HNL5319823',
      defaultOptions: {
        preventAutoTrack: false,
        pagestay: true,
        useOpenId: true,
        appid: 'xxxxxx_appid',
        openid: 'xxxxxx_openid',
        ping_method: 'POST',
        encrypt_mode: 'close',
        enable_compression: false,
        track_interval: 0,
        batch_max_time: 1,
        url: '',
        session_interval: 30 * 60 * 1000,
        // 追加参数是否在hash之后
        afterHash: false
      },
      // 用户身份参数
      anonymous_id: '-',
      tracker: null,
      // 自定义参数
      customData: [{
        key: '',
        value: ''
      }],
      accountData: [{
        key: '',
        value: ''
      }],
      trackNumber: 0
    }
  },
  mounted () {
    this.isMiniProgram = window.__isMiniProgram
    this.isSmallScreen = window.innerWidth < 800
  },
  methods: {
    initTracker () {
      console.log('initTracker ')
      let _options = {}

      Object.entries(this.defaultOptions).forEach(([key, value]) => {
        if (value !== '') {
          _options[key] = value
        }
      })

      window.tracker = QDTracker.init({
        tid: this.tid,
        appkey: this.appkey,
        options: _options
      })
      // window.tracker.setAes(window['__qq_qidian_da_market_AES_method'])
    },
    reload () {
      window.location.reload()
    },
    clearAllCookie () {
      const allCookies = document.cookie.split('; ')

      // 遍历并清空所有Cookie
      allCookies.forEach((cookie) => {
        const cookieName = cookie.split('=')[0]
        const expireDate = new Date(0).toUTCString()

        // 将 Cookie 的值设置为空字符串并设置过期时间以移除 Cookie
        document.cookie = `${cookieName}=; expires=${expireDate}; path=/`
      })
    },
    clearLocalInfo () {
      // 清空localStorage
      window.localStorage.clear()
      // 清空cookie
      this.clearAllCookie()
    },
    updateInfo () {
      // 获取当前anonymous_id
      this.anonymous_id = window.localStorage.getItem('tencentSig')
    },
    sendEvent1 () {
      window.tracker.track('testclick', {
        loc: 'China2:testclick',
        aaa: '123:testclick',
        ccc: Date.now() + ':ccc:testclick',
        ccc2: function () {
          return Date.now() + ':ccc2:dynamic:testclick'
        }
      }, function (error, response) {
        console.log('testclick success', error, response)
      })
    },
    sendEvent2 () {
      window.tracker.track('testclick', {loc: 'shanghi'})
      window.tracker.track('testclick1', {loc: 'shanghi1'})
      window.tracker.track('testclick2', {loc: 'shanghi2'})
      window.tracker.track('testclick3', {loc: 'shanghi3'})
      window.tracker.track('testclick4', {loc: 'shanghi4'})
      window.tracker.track('testclick5', {loc: 'shanghi5'})
      window.tracker.track('testclick6', {loc: 'shanghi6'})
      window.tracker.track('testclick7', {loc: 'shanghi7'})
      window.tracker.track('testclick8', {loc: 'shanghi8'})
      window.tracker.track('testclick9', {loc: 'shanghi9'})
      window.tracker.track('testclick10', {loc: 'shanghi10'})
    },
    sendEvent3 (number) {
      for (let i = 0; i < number; i++) {
        window.tracker.track(`testclick${i}`)
      }
    },
    setCommonData () {
      let _commonData = {};
      (this.customData || []).forEach(data => {
        if (data.key) {
          _commonData[data.key] = data.value
        }
      })
      _commonData.ddd2 = () => {
        return 'commonData:ddd2:dynamic:' + Date.now()
      }
      _commonData.ddd = 'commonData:ddd:' + Date.now()

      window.tracker.setCommonData(_commonData)
    },
    setAccountData () {
      let _commonData = {};
      (this.accountData || []).forEach(data => {
        if (data.key) {
          _commonData[data.key] = data.value
        }
      })
      window.tracker.setAccountInfo(_commonData)
    },
    setDomain () {
      window.tracker.setDomain(this.defaultOptions.url)
    },
    jumpSPA () {
      window.tracker.reset()
      this.$router.push('page')
    },
    testWebWorker () {
      const workerTimer = window.__qda_workerTimer__
      setTimeout(() => {
        console.warn('window.setTimeout:', this.timeout, this.count++)
      }, this.timeout)

      this.timerId = workerTimer.setTimeout(() => {
        console.warn('webWorker.setTimeout:', this.timeout, this.count++)
      }, this.timeout)
      console.log('this.timerId==', this.timerId)
    },
    clearTimer () {
      const workerTimer = window.__qda_workerTimer__
      workerTimer.clearTimeout(this.timerId)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.sdk-track-demo {
  padding: 0 100px;
  margin: 0 auto;
  overflow: hidden;
}

.sdk-track-demo.is-miniprogram {
  padding: 0 10px;
}

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
strong {
  display: block;
  margin-top: 15px;
}
.block {
  border: 1px #ccc solid;
  margin: 15px;
  padding: 0 15px;
}
.sdk-track-demo.is-miniprogram .block {
  margin: 5px;
}

.t-button {
  margin: 5px 0;
}
</style>



