// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
// 引入组件库的少量全局样式变量
import VueRouter from 'vue-router'
import configRouter from './route.config'
import { checkMiniProgram } from './utils/wx'
import 'element-ui/lib/theme-chalk/index.css'
import ElementUI from 'element-ui'

window.__isMiniProgram = checkMiniProgram()

const pageTypes = process.env.pagesType
console.log('process.env.pagesType', pageTypes)

let routeConfig
if (pageTypes === 'github') {
  // 适配githubs pages
  routeConfig = {
    base: '/repos/dist',
    routes: configRouter,
    mode: 'hash',
    fallback: false
  }
} else {
  routeConfig = {
    base: '',
    routes: configRouter,
    mode: 'history',
    fallback: false
  }
}

const router = new VueRouter(routeConfig)

Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.use(VueRouter)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  router,
  template: '<App/>'
})



