let route = [
  {
    path: '/',
    name: 'home',
    component: r => require.ensure([], () => r(require('./components/Demo.vue')), 'home')
  }, {
    path: '/page',
    name: 'page',
    meta: {
      title: '跳转page'
    },
    component: r => require.ensure([], () => r(require('./components/spa.vue')), 'spa')
  }
]

module.exports = route



